import { Component, OnInit, Input } from '@angular/core';
import { ViewCell } from 'ng2-smart-table';
@Component({
  selector: 'app-emoloyeeoptions',
  styleUrls: ['./custom.component.scss'],
  template: `
  <div class="btn-group">
  <button type="button" class="btn btn-info dropdown-toggle"
            data-toggle="dropdown" aria-haspopup="true"
            aria-expanded="false">
            خيارات
  </button>
  <div class="dropdown-menu">
            <a class="dropdown-item" href="#" [routerLink]="['/employee-view-assign']">عرض <i
                                class="fa fa-fw fa-eye text-blue"></i></a>
            <a class="dropdown-item" href="#">تعديل<i
                                class="fa fa-fw fa-edit text-blue"></i></a>
           
          
            <div class="dropdown-divider"></div>
            <a class="dropdown-item" href="#">حذف<i
                                class="fa fa-fw fa-trash text-red"></i></a>
  </div>
</div>
  `,
})
export class EmoloyeeOptionsComponent implements OnInit {
  @Input() value;
  constructor() { }

  ngOnInit() {
    $('.dropdown-toggle').each(function() {
   
      $(this).on('click', function(e) {
        e.stopPropagation();
        $('.dropdown-menu').removeClass('show');
        $(this).parent().find('.dropdown-menu').toggleClass('show');
      });
    });

    $(document).on('click', () => {
      $('.dropdown-menu').removeClass('show');
    });

    $('.dropdown-menu').on('click', (e) => {
      e.stopPropagation();
    });
  }
 

}
