import {Component, OnInit} from '@angular/core';
import * as DT_Required_Class from './datatables_required_functions';
import {ActivatedRoute, Router} from '@angular/router';
// import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';

@Component({
  templateUrl: './table.html',
  styleUrls: ['./tables.css']
})
export class BranchesComponent implements OnInit {
  constructor(private router: Router, private activatedRouters: ActivatedRoute) {
  }

  ngOnInit() {

    let table = null;
    const This = this;
    // tslint:disable-next-line:variable-name
    const Translate_Params = new DT_Required_Class.DT_Functions(table);
    const Fields = Translate_Params.getFields('branches');
    console.log(Fields);
    const Link = Translate_Params.TablePath + '/table.branches.php';
    const TableID = 'table_layout';
    const editor =  new (<any> ($.fn)).dataTable.Editor({
      ajax: Link,
      table: '#' + TableID,
      fields: [
        {
          label: Fields['branches'].branch_name + ':',
          name: 'branches.branch_name'
        },
        {
          label: Fields['branches'].address + ':',
          name: 'branches.address'
        }]
    });

    table = table = $('#' + TableID).DataTable(<DataTables.Settings> {
      dom: 'Bfrtip',
      ajax: Link,

      initComplete() {
        Translate_Params.setCompleteinitObject(this, table);
        return Translate_Params.Completeinit();
      },
      columns: [

        {
          data: 'branches.id',
          searchable: false,
          orderable: false,
          targets: 0,
          width: '2%'
        },

        {
          title: Fields['branches'].branch_name + ':',
          className: 'Filter_Text',
          data: 'branches.branch_name'
          , render(data, type, full, meta) {
            return type === 'display' && data != null && data.length > 40 ? '<span >' + data.substr(0, 38) + '...</span>' : data;
          }
        },
        {
          title: Fields['branches'].address + ':',
          className: 'Filter_Text',
          data: 'branches.address'
          , render(data, type, full, meta) {
            return type === 'display' && data != null && data.length > 40 ? '<span >' + data.substr(0, 38) + '...</span>' : data;
          }
        }],
      select: true,
      lengthChange: false,
      responsive: true,

      buttons: [
        {extend: 'create', editor},
        {extend: 'edit', editor},
        {extend: 'remove', editor},
        {extend: 'colvis', columns: ':gt(0)', text: 'Select Columns', editor},
        {
          extend: 'collection',
          text: 'Export',
          buttons: [
            {extend: 'copy', editor},
            {
              extend: 'excel', editor, exportOptions: {
                // columns: [ 0, ':visible' ]
              }
            },
            {
              extend: 'print'
              , customize(win) {
                $(win.document.body).find('table')
                  .addClass('compact')
                ;
                $(win.document.body).prepend('');
              }, text: 'Print', editor, exportOptions: {
                columns: [0, ':visible']
              }
            },


          ]
        }
      ]


    } );

    table.on('order.dt search.dt', function() {
      table.column(0, {search: 'applied', order: 'applied'}).nodes().each(function(cell, i) {
        cell.innerHTML = i + 1;
      });
    }).draw();

    table.on('error.dt', function(e, settings, techNote, message) {
      console.log('An error has been reported by DataTables: ', message);
    });
    $(document).off('click', '.ROUTERP');
    $(document).on('click', '.ROUTERP', function(event) {
      This.router.navigate([$(this).attr('routes')]);
    });
  }
}
