export class DT_Functions {
  table;
  footerCallback;
  CompleteinitObject;
  MainPath = '';
  TablePath = '';

  constructor(table) {
    this.table = table;
    this.MainPath = 'http://darweesh.egyprosoft.com/';
    this.TablePath = 'http://darweesh.egyprosoft.com/Backend/php';
  }

  TranslateAr = {
    create: {
      button: 'إضافة',
      title: 'إضافة جديد',
      submit: 'إضافة'
    },
    edit: {
      button: 'تعديل',
      title: 'تعديل',
      submit: 'تعديل'
    },
    remove: {
      button: 'حذف',
      title: 'حذف',
      submit: 'حذف'

    },
    collection: {
      text: 'نسخ'
    },
    print: {
      title: 'طباعة'
    },
    error: {
      system: 'حدث خطأ'
    }
  };

  setAPI(footerCallback: any) {
    this.footerCallback = footerCallback;
  }

  Completeinit = function() {
    let first = true;
    const ThisClass = this;
    let i = 0;
    const FilterDiv = $('#SearchFilter');
    FilterDiv.addClass('row');
    FilterDiv.append('<input type="hidden" id="fini" value=""/>\n' +
      '<input type="hidden" id="ffin" value=""/>\n' +
      '<input type="hidden" id="SCol" value=""/>\n' +
      '<input type="hidden" id="ECol" value=""/>');
    this.CompleteinitObject.api().columns().every(function() {
      const column = this;
      const title = $(column.header()).text();
      if (first) {
        first = false;
        i++;
        return true;
      }

      const Field = $('<div class="SearchField col-md-2" id="F_' + i + '"></div>');

      if (($(column.header()).hasClass('Filter_MSelect'))) {
        const select = $('<select id="F_' + i + '" class="chosen-select  form-control" style="width: 100%" ><option value="">' + title + '</option></select>').appendTo(Field).on('change',
          function() {
            const val = $.fn.dataTable.util.escapeRegex(($(this) as any).val());
            column.search(val ? '^' + val + '$' : '', true, false).draw();
          });
        column.cache('search').unique().sort().each(
          function(d, j) {
            if (d != null) {
              select.append('<option value="' + d + '">' + d + '</option>');
            }
          });
      } else if ($(column.header()).hasClass('Filter_Date')) {
        const Input = $('<input type="text" class="datePick" placeholder="' + title + '" />').appendTo(Field);
      } else if ($(column.header()).hasClass('Filter_Datetime')) {
        const Input = $('<input type="text" id="F_' + i + '" class="dateTimePick" placeholder="' + title + '" />').appendTo(Field);
      } else if ($(column.header()).hasClass('Filter_Number')) {
        const select = $('<select id="F_' + i + '" class="chosen-select  form-control Filter_Number" style="width: 100%" ><option value="">' + title + '</option></select>').appendTo(Field).on('change',
          function() {
            const val = $.fn.dataTable.util.escapeRegex(($(this) as any).val());
            column.search(val ? '^' + val + '$' : '', true, false).draw();
          });
        column.cache('search').unique().sort().each(
          function(d, j) {
            if (d != null) {
              select.append('<option value="' + d + '">' + d + '</option>');
            }
          });
      } else if (!$(column.header()).hasClass('Filter_Total')) {
        const Input = $('<input type="text" placeholder="' + title + '" />').appendTo(Field);
      }
      if (!$(column.header()).hasClass('Filter_None')) {
        $(FilterDiv).append(Field);
      }
      ($('.datePick') as any).daterangepicker(
        {
          showDropdowns: true,
          autoUpdateInput: false,
          locale: {
            format: 'DD/MM/YYYY',
            cancelLabel: 'Clear'
          }
        });
      ($('.dateTimePick') as any).daterangepicker({
        showDropdowns: true,
        timePicker: true,
        timePickerIncrement: 30,
        autoUpdateInput: false,
        locale: {
          format: 'DD/MM/YYYY h:mm A',
          cancelLabel: 'Clear'
        }
      });


      $('.datePick').on('apply.daterangepicker', function(ev, picker) {
        $(this).val(picker.startDate.format('ddd, D MMM YYYY h:mm a') + ' - ' + picker.endDate.format('ddd, D MMM YYYY h:mm a'));
        let ID = $(this).closest('th').attr('id');
        ID = ID.replace('F_', '');
        $('#fini').val(picker.startDate.format('ddd, D MMM YYYY h:mm a'));
        $('#ffin').val(picker.endDate.format('ddd, D MMM YYYY h:mm a'));
        $('#SCol').val(ID);
        $('#ECol').val(ID);
        ThisClass.table.draw();
      });

      $('.dateTimePick').on('apply.daterangepicker', function(ev, picker) {
        $(this).val(picker.startDate.format('ddd, D MMM YYYY h:mm a') + ' - ' + picker.endDate.format('ddd, D MMM YYYY h:mm a'));
        let ID = $(this).closest('th').attr('id');
        ID = ID.replace('F_', '');
        $('#fini').val(picker.startDate.format('ddd, D MMM YYYY h:mm a'));
        $('#ffin').val(picker.endDate.format('ddd, D MMM YYYY h:mm a'));
        $('#SCol').val(ID);
        $('#ECol').val(ID);
        ThisClass.table.draw();
      });

      $('.datePick').on('cancel.daterangepicker', function(ev, picker) {
        let ID = $(this).closest('th').attr('id');
        ID = ID.replace('F_', '');
        $('#SCol').val(ID);
        $('#ECol').val(ID);
        $('#fini').val('');
        $('#ffin').val('');
        $(this).val('');
        ThisClass.table.draw();
      });

      $('.dateTimePick').on('cancel.daterangepicker', function(ev, picker) {
        let ID = $(this).closest('th').attr('id');
        ID = ID.replace('F_', '');
        $('#SCol').val(ID);
        $('#ECol').val(ID);
        $('#fini').val('');
        $('#ffin').val('');
        $(this).val('');
        ThisClass.table.draw();
      });
      i++;
    });
    ($('.chosen-select') as any).chosen({rtl: true, allow_single_deselect: true});
    $('.SearchField').find('input').addClass('form-control');
    $('.SearchField').find('select').addClass('form-control');
    $('.SearchField').on('keyup', function() {
      let str = this.id;
      str = str.replace('F_', '');
      if ($(this).find('input').hasClass('Filter_Number')) {
        const val = $.fn.dataTable.util.escapeRegex($(this).find('input').val().toString());
        ThisClass.table
          .columns(parseInt(str))
          .search(val ? '^' + val + '$' : '', true, false)
          .draw();
      } else {
        ThisClass.table
          .columns(parseInt(str))
          .search($(this).find('input').val())
          .draw();
      }
    });
  };

  setCompleteinitObject(initComplete: any, table: any) {
    this.table = table;
    this.CompleteinitObject = initComplete;
  }

  getFields(Requests: string) {

    return this.getJSON('assets/Language/ar/table.' + Requests + '.json');

  }
   getJSON(url: string) {
    let Resdata = [];
    $.ajax({url, method: 'get', dataType: "json", async: false, success(data) {
        Resdata = data;
      }});
    return Resdata;
  }
}
