        setTimeout(()=>{GenerateDrop();},100);
    
        function GenerateDrop() {
          $('.side-navigation .has-items').each(function() {
            $(this).on('click', function(e) {
              e.preventDefault()
              $('.side-navigation .has-items').removeClass('open-has-items');
              $('.side-navigation .link-items-content').removeClass('open-items-content');
              $(this).toggleClass('open-has-items');
              $(this).parent().find('.link-items-content').toggleClass('open-items-content');
            });
    
          });
          $('.side-navigation .link-items-content .active').each(function() {
            $(this).closest('.link-items-content').toggleClass('open-items-content');
            $(this).closest('.link-items-content').parent().find('.has-items').toggleClass('open-has-items');
    
    
    
          });
          $('.show-side-mobile').on('click', function() {
            $('.left-layout').toggleClass('show-left-layout')
          });
        }
